import React from 'react';

import { evidence } from '../data';

import { Filters, Toggles } from '../types/Filters';

import EvidenceViewer from './EvidenceMain';
import FilterBox from './FilterBox';
import filterEvidence from './FilterBox/filterEvidence';
import { PATHS } from './Routes';
import { usePageTitle, useQueryParam } from './shared/hooks';

const EvidenceApp = () => {
  usePageTitle('January 6 Evidence Explorer');

  const [rawLocation, setRawLocation] = useQueryParam('location', { array: true });
  const [rawSuspect, setRawSuspect] = useQueryParam('suspect', { array: true });
  const [rawOtherTags, setRawOtherTags] = useQueryParam('other_tags', { array: true });
  const [rawKeywords, setRawKeywords] = useQueryParam('keywords', { array: true });
  const [rawStartEndTimes, setRawStartEndTimes] = useQueryParam('start_end_times');

  const filters = {
    location: rawLocation ? new Set<string>(rawLocation) : new Set<string>(),
    suspect: rawSuspect ? new Set<string>(rawSuspect) : new Set<string>(),
    otherTags: rawOtherTags ? new Set<string>(rawOtherTags) : new Set<string>(),
  };
  const setFilters = <K extends keyof Filters>(key: K, newValue: Filters[K]): void => {
    if (key === 'location') setRawLocation(Array.from(newValue));
    if (key === 'suspect') setRawSuspect(Array.from(newValue));
    if (key === 'otherTags') setRawOtherTags(Array.from(newValue));
  };
  // eslint-disable-next-line
  const keywords = rawKeywords ? (Array.isArray(rawKeywords) ? rawKeywords : [rawKeywords]) : [];
  const setKeywords = (newKeywords: string[]): void => setRawKeywords(newKeywords);
  const [toggles, setToggles] = React.useState<Toggles>({
    includeUntimestamped: true,
  });
  const startEndTimes: [Date, Date] | null = rawStartEndTimes
    ? [
        new Date(parseInt(rawStartEndTimes.split(',')[0], 10)),
        new Date(parseInt(rawStartEndTimes.split(',')[1], 10)),
      ]
    : null;
  const setStartEndTimes = (newTimes: [Date, Date] | null) => {
    if (newTimes) {
      setRawStartEndTimes(`${newTimes[0].getTime()},${newTimes[1].getTime()}`);
    } else {
      setRawStartEndTimes(null);
    }
  };

  const { filteredEvidence, timelineFilteredEvidence, filterChoices } = filterEvidence(
    evidence,
    filters,
    keywords,
    toggles,
    startEndTimes
  );

  return (
    <div>
      <FilterBox
        onFilterChanged={setFilters}
        onKeywordsChanged={setKeywords}
        filters={filters}
        choices={filterChoices}
        keywords={keywords}
        toggles={toggles}
        onToggleChanged={(toggle, newValue) => {
          setToggles((oldToggles) => ({ ...oldToggles, [toggle]: newValue }));
        }}
      />
      <EvidenceViewer
        title="January 6 Evidence Explorer"
        evidence={filteredEvidence}
        timedEvidence={timelineFilteredEvidence}
        startEndTimes={startEndTimes}
        setStartEndTimes={setStartEndTimes}
        hideExplorerLink
        rootPath={PATHS.evidence}
        getDetailPath={(detailId) => `${PATHS.evidence}/${detailId}`}
      />
    </div>
  );
};

export default EvidenceApp;
